<template>
    <div class="instrument">
        <div class="bread_crumb">
            <!--<span>位置：主页 | </span>-->
            <span>当前位置：</span>
            <span class="bread_title" @click="goHome()">库存管理</span>
            <span>| 小类管理</span>
        </div>
        <div class="stock_title">
            <span>库存物品详细信息</span>
            <el-select class="select_input"  v-model="instrumentOptionsValue" @change="changeInstrumentType()">
                <el-option v-for="item in instrumentSearchOptions" :key="item.value"
                           :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-button :class="selectTable.length != 0 ? 'right_btn' : 'disClick' "  type="primary" @click="deleteInstrument()">删除</el-button>
            <el-button :class="selectTable.length != 0 ? 'right_btn' : 'disClick' "  type="primary" @click="editInstrument()">编辑</el-button>
            </div>
        <!--详情信息-->
        <el-table class="instrument_table" :data="instrumentData" ref="selTable" stripe
                  style="width: 100%" @selection-change="instrumentSelectionChange"

                  highlight-current-row  @current-change="checkList"

                  @select="checkInstrumentSingle" @select-all="checkInstrumentAll">
            <el-table-column type="selection" width="55">全选</el-table-column>
            <el-table-column prop="item_id" label="物品编号"></el-table-column>
            <el-table-column prop="pic" label="物品图片" >
                <template slot-scope="scope">
                    <img v-if="scope.row.pic" :src="$store.state.operationPhotoPath + scope.row.pic" alt="">
                    <img v-if="!scope.row.pic" src="../../static/pic_yaoji.png" alt="">
                </template>
            </el-table-column>

            <el-table-column prop="item_name" label="物品名称"></el-table-column>
            <!--<el-table-column label="物品名称">-->
                <!--<template slot-scope="scope">-->
                    <!--<span :style="clickIndex == scope.row.item_id ? 'color: #5ca8e7;' : ''" class="name" @click="checkList(scope.row)">{{ scope.row.item_name }}</span>-->
                <!--</template>-->
            <!--</el-table-column>-->
            <el-table-column prop="sort_name" label="大类名称" ></el-table-column>
            <el-table-column prop="dangerous" label="分类"  ></el-table-column>
            <el-table-column label="状态" >
                <template slot-scope="scope">
                    <span>{{ scope.row.status | article_status }}</span>
                </template>
            </el-table-column>
            <el-table-column label="存量">
                <template slot-scope="scope">
                    <span>{{ scope.row.stock }}{{ scope.row.unit }}</span>
                </template>
            </el-table-column>
            <el-table-column label="入库时间">
                <template slot-scope="scope">
                    <span>{{ scope.row.created_at | c_at}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="overdue_at" label="下次维护时间"></el-table-column>
            <el-table-column prop="s_content" label="存放位置" show-overflow-tooltip></el-table-column>
            <el-table-column prop="introduce" label="备注" show-overflow-tooltip></el-table-column>
        </el-table>
        <div class="block" style="text-align: center;margin-top: 30px;">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageIndex"
                    :page-size="5"
                    layout="total, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>
        <div class="stock_tabs" v-if="showRecords">
            <ul class="tabs_lists">
                <li :class="tableIndex == 1 ? 'stock_tabs_active' : ''" @click="changeTable(1)">
                    历史领用记录
                    <span></span>
                </li>
                <li :class="tableIndex == 2 ? 'stock_tabs_active' : ''" @click="changeTable(2)">
                    历史归还记录
                    <span></span>
                </li>
            </ul>
            <!--历史领用记录-->
            <el-table class="history_table" v-show="tableIndex == 1" :data="receivedData" stripe max-height="426"
                      style="width: 100%">
                <el-table-column prop="user_name" label="申领人" width="180"></el-table-column>
                <el-table-column prop="receive_at" label="领用时间" width="180"></el-table-column>
                <el-table-column prop="overdue_at" label="逾期时间" width="180"></el-table-column>
                <el-table-column prop="item_use" label="用途"></el-table-column>
                <el-table-column prop="approver" label="审批人"></el-table-column>
                <el-table-column prop="recipient" label="经办人"></el-table-column>
                <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
            <!--归还记录-->
            <el-table class="received_table" v-show="tableIndex == 2" :data="returnData" stripe max-height="426"
                      style="width: 100%">
                <el-table-column prop="user_name" label="申领人" width="180"></el-table-column>
                <!--<el-table-column prop="overdue_time" label="逾期时间" width="180"></el-table-column>-->
                <el-table-column prop="created_at" label="归还时间" width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.created_at | return_date}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="usage" label="归还数量" width="180"></el-table-column>
                <el-table-column label="是否逾期">
                    <template slot-scope="scope">
                        <span>{{ scope.row.is_overdue | overdue_status }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="recipient" label="接收人"></el-table-column>
                <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
        </div>
        <!--编辑-->
        <div class="instrument_edit" v-if="editStatus">
            <div class="edit">
                <div class="edit_title">
                    <span>编辑</span>
                    <i class="el-icon-close" @click="closeEditBox()"></i>
                </div>
                <div class="edit_main">
                    <el-form :model="editData" :rules="rules" ref="editData" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="名称" prop="item_name">
                            <el-input readonly v-model="editData.item_name"></el-input>
                        </el-form-item>
                        <div class="right_picture">
                            <el-upload class="avatar-uploader" ref="upload" action="/api/item-edit"
                                       :show-file-list="false" accept="image/*"
                                       :on-success="handleAvatarSuccess"
                                       :headers="myToken"
                                       :on-change="handleChangeEditPic" :data="uploadEditFile"
                                       :file-list="fileList3" :auto-upload="false"
                            >
                                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                <img v-if="!imageUrl" src="../../static/pic_yaoji.png" class="avatar">
                                <!--<i v-else class="el-icon-plus avatar-uploader-icon"></i>-->
                                <el-button class="up">修改图片</el-button>
                            </el-upload>
                        </div>
                        <el-form-item label="大类名称" prop="sort_id">
                            <el-select  class="add_input" v-model="editData.sort_id">
                                <el-option v-for="(item,index) in sortList" :key="item.id"
                                           :label="item.name" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <!--入库数量是否可编辑-->
                        <el-form-item v-if="editData.status == 1" label="入库数量" prop="stock">
                            <el-input readonly v-model="editData.stock"></el-input>
                        </el-form-item>
                        <el-form-item v-else="editData.status == 1" label="入库数量" prop="stock">
                            <el-input v-model="editData.stock"></el-input>
                        </el-form-item>
                        <el-form-item label="单位" prop="unit">
                            <el-select  class="add_input" v-model="editData.unit" placeholder="">
                                <el-option v-for="(item,index) in unitList" :key="item.label"
                                           :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="分类" prop="dangerous">
                            <el-select  class="add_input" v-model="editData.dangerous">
                                <el-option v-for="(item,index) in dangerousList" :key="item.label"
                                           :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item class="time" label="下次维修时间">
                            <el-date-picker type="date" @change="changeEditDate()"
                                            v-model="editData.overdue_at"
                                            :picker-options="pickerOptions1">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item class="label_id" label="电子标签ID" prop="frid_number">
                            <el-input v-model="editData.frid_number" disabled></el-input>
                        </el-form-item>
                        <el-form-item class="address" label="存放位置" prop="">
                            <el-input v-model="editData.s_content"></el-input>
                            <el-button type="primary" @click="chooseAddress()">选择位置</el-button>
                        </el-form-item>
                        <el-form-item label="备注" prop="introduce">
                            <el-input type="textarea" v-model="editData.introduce"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="btn">
                        <el-button @click="submitEdit('editData')">确认</el-button>
                    </div>
                </div>
            </div>
        </div>
        <!--地图-->
        <div class="map_box" v-if="mapStatus">
            <div class="shelf_map">
                <div class="map_title">
                    <span>选择上架位置</span>
                    <i class="el-icon-close" @click="closeShelfMap()"></i>
                </div>
                <div class="tip">
                    <span>当前所选位置：</span>
                    <span class="address">{{ selectLocation.name }}</span>
                </div>
                <div class="right_list" id="sideBox">
                    <ul>
                        <li :class="index == selectIndex ? 'is_active' : ''"
                            @click="chooseLocationRightClick(item,index)"
                            v-for="(item,index) in pointData" :key="index">
                            {{ item.name }}
                            <span class="iconfont el-icon-pharmaceutical_administration-dingwei-"></span>
                        </li>
                    </ul>
                </div>
                <div class="map_main">
                    <img src="../assets/shelf_map.jpg" alt="">
                    <ul class="point_list">
                        <li :class="key == selectIndex ? 'active' : ''" @click="chooseLocationClick(item,key)"
                            class="iconfont el-icon-pharmaceutical_administration-dingwei-"
                            :style="'position: absolute;top: ' + item.y + 'px;left:' + item.x + 'px;'"
                            v-for="(item,key) in pointData" :key="key">
                            <div :style="key == selectIndex ? 'display:block;' : 'display:none;'">
                                {{ item.name }}
                                <span></span>
                            </div>
                        </li>
                    </ul>
                    <div class="btn">
                        <el-button @click="confirmShelfLocation()">确认上架位置</el-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    var token =  'Bearer ' + JSON.parse(localStorage.getItem('user')).token;
    export default {
        data() {
            return {
                pickerOptions1: {
                    disabledDate(time) {
                        return (time.getTime()+3600*24*1000) < Date.now();
                    },
                },
                //搜索条件
                instrumentSearchOptions: [
                    {
                        value: '2',
                        label: '全部'
                    },
                    {
                        value: '1',
                        label: '已借出'
                    },
                    {
                        value: '0',
                        label: '在仓库'
                    },
                ],
                instrumentOptionsValue: '2',
                instrumentData: [],         //小类列表
                selectTable: [],            //多选列表
                pageIndex: 1,       //第几页
                pageTotal: 0,
                dangerousList: [
                    {
                        label: '危险品',
                    },
                    {
                        label: '非危险品',
                    },
                ],               //分类列表
                //编辑
                editStatus: false,                  //是否显示编辑框
                editData: {
                    item_name: '',
                    type: '',
                    sort_id: '',
                    stock: '',
                    unit: '',
                    overdue_at: '',
                    frid_id: '',
                    dangerous:'',
                    frid_number: '',
                    introduce: '',
                    file: {},
                },
                rules: {
                    item_name: [
                        {required: true, message: '请输入名称', trigger: 'blur'},
                    ],
                    sort_id: [
                        {required: true, message: '请选择分类', trigger: 'change'}
                    ],
                    stock: [
                        {required: true, message: '请输入入库数量', trigger: 'blur'}
                    ],
                    unit: [
                        {required: true, message: '请选择单位', trigger: 'change'}
                    ],
                    overdue_date: [
                        {type: 'date', required: true, message: '请选择日期', trigger: 'change'}
                    ],
                    frid_id: [
                        {required: true, message: '请输入标签ID', trigger: 'blur'}
                    ],
                    frid_number: [
                        {required: true, message: '请输入标签ID', trigger: 'blur'}
                    ],
                },
                imageUrl: '',
                uploadEditFile: {
                    item_id: '',
                    type: '',
                    item_name: "",
                    sort_id: "",
                    frid_id: '',
                    stock: '',
                    unit: '',
                    overdue_at: '',
                    introduce: '',
                    situation: '',
                    s_content: '',
                    is_putaway: 0
                },
                myToken: {Authorization: token},
                fileList3: [],
                sortList: [],                       //分类列表
                unitList: [
                    {
                        label: 'g',
                    },
                    {
                        label: 'ml',
                    },
                    {
                        label: '个',
                    },
                ],
                //地图
                mapStatus: false,
                pointData: [
                    //A
                    {
                        name: 'E仓库A1-1',
                        x: '265',
                        y: '325',
                    },
                    {
                        name: 'E仓库A1-2',
                        x: '305',
                        y: '275',
                    },
                    {
                        name: 'E仓库A1-3',
                        x: '345',
                        y: '225',
                    },
                    {
                        name: 'E仓库A1-4',
                        x: '385',
                        y: '175',
                    },
                    {
                        name: 'E仓库A1-5',
                        x: '425',
                        y: '125',
                    },
                    {
                        name: 'E仓库A1-6',
                        x: '465',
                        y: '75',
                    },
                    //B
                    {
                        name: 'E仓库B1-1',
                        x: '400',
                        y: '390',
                    },
                    {
                        name: 'E仓库B1-2',
                        x: '440',
                        y: '340',
                    },
                    {
                        name: 'E仓库B1-3',
                        x: '480',
                        y: '290',
                    },
                    {
                        name: 'E仓库B1-4',
                        x: '520',
                        y: '240',
                    },
                    {
                        name: 'E仓库B1-5',
                        x: '560',
                        y: '190',
                    },
                    {
                        name: 'E仓库B1-6',
                        x: '600',
                        y: '140',
                    },
                    //C
                    {
                        name: 'E仓库C1-1',
                        x: '540',
                        y: '455',
                    },
                    {
                        name: 'E仓库C1-2',
                        x: '580',
                        y: '405',
                    },
                    {
                        name: 'E仓库C1-3',
                        x: '620',
                        y: '355',
                    },
                    {
                        name: 'E仓库C1-4',
                        x: '660',
                        y: '305',
                    },
                    {
                        name: 'E仓库C1-5',
                        x: '700',
                        y: '255',
                    },
                    {
                        name: 'E仓库C1-6',
                        x: '740',
                        y: '205',
                    },
                    //D
                    {
                        name: 'E仓库D1-1',
                        x: '670',
                        y: '515',
                    },
                    {
                        name: 'E仓库D1-2',
                        x: '710',
                        y: '465',
                    },
                    {
                        name: 'E仓库D1-3',
                        x: '750',
                        y: '415',
                    },
                    {
                        name: 'E仓库D1-4',
                        x: '790',
                        y: '365',
                    },
                    {
                        name: 'E仓库D1-5',
                        x: '830',
                        y: '315',
                    },
                    {
                        name: 'E仓库D1-6',
                        x: '870',
                        y: '265',
                    },

                ],
                oldLocation: '',        //原本的位置
                selectLocation: '',             //选择的点的位置
                selectIndex: '0',               //选择第几个点
                //记录
                tableIndex: '1',            //显示哪个标签
                frid_id: '',        //点击的小类frid_id
                clickIndex: '',
                showRecords: false,
                //历史领用记录
                receivedData: [],
                //历史归还记录
                returnData: [],
            }
        },
        created() {
            this.$store.state.sideIndex = 1;
            this.getList();
            this.getSortId();
        },
        mounted() {
            //使select的input的光标消失（IE  360兼容模式）
            let inputs = document.getElementsByClassName('select_input')[0].childNodes[1].children[0]
            inputs.setAttribute("unselectable", "on");
        },
        methods: {
            //获取分类
            getSortId() {
                this.$axios.post('/sort-list').then((response) => {
//            		console.log(response);
                    this.sortList = response.data.data;
                })
            },
            goHome() {
                this.$router.push('/homepage')
            },
            //获取大类列表查询小类
            getList() {
                //获取小类列表
                this.$axios.post('/list', {
                    item_type: "仪器设备",
                    number: 5,
                    sort_id: this.$route.params.id,
                    status: this.instrumentOptionsValue,
                }).then((res) => {
//                    console.log(res)
                    if (res.data.data.data == undefined) {
                        this.instrumentData = [];
                        this.pageTotal = 0;
                    } else {
                        this.instrumentData = res.data.data.data;
                        this.pageTotal = res.data.data.total;
                        for (let i in this.instrumentData) {
                            if (this.instrumentData[i].photo == null) {
                                this.instrumentData[i].pic = null
                            } else {
                                this.instrumentData[i].pic = this.instrumentData[i].photo.split('/')[1]
                            }
                        }
                    }
                }).catch((err) => {})
            },
            //查询状态
            changeInstrumentType() {
                this.selectTable = [];
                this.$refs.selTable.clearSelection();
                //获取小类列表
                this.$axios.post('/list', {
                    item_type: "仪器设备",
                    number: 5,
                    sort_id: this.$route.params.id,
                    status: this.instrumentOptionsValue,
                }).then((res) => {
//                  console.log(res)
                    this.showRecords = false;
                    this.receivedData = [];
                    if (res.data.data) {
                        this.instrumentData = res.data.data.data;
                        this.pageTotal = res.data.data.total;
                        for (let i in this.instrumentData) {
                            if (this.instrumentData[i].photo == null) {
                                this.instrumentData[i].pic = null
                            } else {
                                this.instrumentData[i].pic = this.instrumentData[i].photo.split('/')[1]
                            }
                        }
                    } else {
                        this.instrumentData = [];
                        this.pageTotal = 0;
                    }
                }).catch((err) => {
                })
            },
            //选取编辑的图片
            handleChangeEditPic(file, fileList) {
//            	console.log(file)
                this.editData.file = file;
                this.imageUrl = file.url;
            },
            //编辑
            editInstrument() {
//                console.log(this.selectTable[0]);
                if (this.selectTable.length == 1) {
                    //编辑操作
                    this.editStatus = true;
                    this.oldLocation = this.selectTable[0].s_content;
                    this.editData = this.selectTable[0];
                    if (this.editData.photo) {
                        this.imageUrl = this.$store.state.operationPhotoPath + this.editData.photo.split('/')[1];
                    } else {
                        this.imageUrl = '';
                    }

                    this.$nextTick(function () {
                        //使select的input的光标消失（IE  360兼容模式）
                        let inputs = document.getElementsByClassName('add_input')
//                        console.log(inputs)
                        for(let i=0;i<inputs.length;i++){
                            let input = inputs[i].childNodes[1].children[0];
                            input.setAttribute("unselectable", "on");
//                            console.log(inputs[i].childNodes[1])
                        }
                    });
                } else if (this.selectTable.length == 0) {
                    this.$message({
                        message: '请选择需要编辑的物品！',
                        type: 'warning',
                        duration: '1500',
                    });
                } else {
                    this.$message({
                        message: '只能选取一项进行编辑操作！',
                        type: 'warning',
                        duration: '1500',
                    });
                }
            },
            closeEditBox() {
                this.selectTable = [];
                this.$refs.selTable.clearSelection();
                this.resetForm('editData');
                this.editData.file = {};
                this.imageUrl = '';
                this.editStatus = false;
                this.handleCurrentChange(this.pageIndex);
                this.$message({
                    type: 'info',
                    message: '已取消编辑',
                    duration: '1500',
                });
            },
            //编辑确认
            submitEdit(formName) {
                console.log(this.editData)
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if (this.editData.file == undefined) {
                            //没有选取图片  用默认图
                            //判断是否修改了位置
                            let param = '';
                            if (this.oldLocation == this.editData.s_content) {
                                //没有修改
                                param = {
                                    item_id: this.editData.item_id,
                                    item_name: this.editData.item_name,
                                    sort_id: this.editData.sort_id,
                                    stock: this.editData.stock,
                                    unit: this.editData.unit,
                                    dangerous: this.editData.dangerous,
                                    overdue_at: this.editData.overdue_at,
                                    frid_id: this.editData.frid_id,
                                    introduce: this.editData.introduce,
                                    is_putaway: 0,
                                    type: "仪器设备",
                                    status: this.editData.status,   //借用状态
                                };
                            } else {
                                param = {
                                    item_id: this.editData.item_id,
                                    item_name: this.editData.item_name,
                                    sort_id: this.editData.sort_id,
                                    stock: this.editData.stock,
                                    unit: this.editData.unit,
                                    dangerous: this.editData.dangerous,
                                    overdue_at: this.editData.overdue_at,
                                    frid_id: this.editData.frid_id,
                                    introduce: this.editData.introduce,
                                    is_putaway: 0,
                                    type: "仪器设备",
                                    s_content: this.editData.s_content,
                                    situation: this.editData.situation,
                                    status: this.editData.status,   //借用状态
                                }
                            }
                            this.$axios.post('/item-edit', param).then((response) => {
//								console.log(response);
                                if (response.data.status == '0') {
                                    this.handleCurrentChange(this.pageIndex);
                                    this.editStatus = false;
                                    this.selectTable = [];
                                    this.$refs.selTable.clearSelection();
                                    this.$message({
                                        type: 'success',
                                        message: '编辑成功！',
                                        duration: '1500',
                                    });
                                } else {
                                    this.$message({
                                        type: 'warning',
                                        message: '编辑失败',
                                        duration: '1500',
                                    });
                                }
                            }).catch((error) => {
                            })
                        } else {
                            if (this.oldLocation == this.editData.s_content) {
                                this.uploadEditFile.item_id = this.editData.item_id;
                                this.uploadEditFile.item_name = this.editData.item_name; //物品名称
                                this.uploadEditFile.sort_id = this.editData.sort_id;
                                this.uploadEditFile.stock = this.editData.stock;
                                this.uploadEditFile.unit = this.editData.unit;
                                this.uploadEditFile.dangerous = this.editData.dangerous;
                                this.uploadEditFile.overdue_at = this.editData.overdue_at;
                                this.uploadEditFile.frid_id = this.editData.frid_id;
                                this.uploadEditFile.introduce = this.editData.introduce;
                                this.uploadEditFile.is_putaway = 0;
                                this.uploadEditFile.type = "仪器设备";
                                this.$refs.upload.submit();
                            } else {
                                this.uploadEditFile.item_id = this.editData.item_id;
                                this.uploadEditFile.item_name = this.editData.item_name; //物品名称
                                this.uploadEditFile.sort_id = this.editData.sort_id;
                                this.uploadEditFile.stock = this.editData.stock;
                                this.uploadEditFile.unit = this.editData.unit;
                                this.uploadEditFile.dangerous = this.editData.dangerous;
                                this.uploadEditFile.overdue_at = this.editData.overdue_at;
                                this.uploadEditFile.frid_id = this.editData.frid_id;
                                this.uploadEditFile.introduce = this.editData.introduce;
                                this.uploadEditFile.is_putaway = 0;
                                this.uploadEditFile.type = "仪器设备";
                                this.uploadEditFile.s_content = this.editData.s_content;
                                this.uploadEditFile.situation = this.editData.situation;
                                this.$refs.upload.submit();
                            }
                        }
                    } else {
                        return false;
                    }
                });
            },
            //上传成功回调
            handleAvatarSuccess(res, file) {
                console.log(res)
                if (res.status == 0) {
                    this.handleCurrentChange(this.pageIndex);
                    this.selectTable = [];
                    this.editStatus = false;
                    this.$refs.selTable.clearSelection();
                    this.$message({
                        type: 'success',
                        message: '编辑成功！',
                        duration: '1500',
                    });
                } else {
                    this.$message({
                        type: 'warning',
                        message: '编辑失败',
                        duration: '1500',
                    });
                }
            },
            //重置表单
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //时间转换
            changeEditDate() {
                if (this.editData.overdue_at == null) {
                    this.editData.overdue_at = '';
                } else {
                    let date = new Date(this.editData.overdue_at);
                    let Y = date.getFullYear() + '-';
                    let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                    let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
                    this.editData.overdue_at = Y + M + D;       //传入后台xxxx-xx-xx
                }
            },
            //选择位置按钮
            chooseAddress() {
                this.mapStatus = true;
                this.selectIndex = 0;
                this.selectLocation = this.pointData[0];
            },
            //选择地图某个位置
            chooseLocationClick(point, val) {
                this.selectIndex = val;
                this.selectLocation = point;
//				console.log(point,val)
                //获取侧边栏进行滚动
                var div = document.getElementById("sideBox");
//				console.log(div)
//				console.log(div.scrollHeight);//总高度
//				div.scrollTop = div.scrollHeight;//到底部
                //前11个
                if (val < 11) {
                    div.scrollTop = 0;
                } else if (val >= 11 && val < 22) {
                    div.scrollTop = 429;
                } else if (val >= 22) {
                    div.scrollTop = 429 * 2;
                }
            },
            //侧边栏选择某个位置
            chooseLocationRightClick(point, val) {
                this.selectIndex = val;
                this.selectLocation = point;
            },
            //确认上架位置
            confirmShelfLocation() {
                //获取选择的位置
                let address = "POINT(" + ((parseInt(this.selectLocation.x) + 15) / 1200).toFixed(3) + " " + ((parseInt(this.selectLocation.y) + 30) / 900).toFixed(3) + ")";
                this.editData.s_content = this.selectLocation.name;
                this.editData.situation = address;
                this.mapStatus = false;
            },
            //关闭上架地图
            closeShelfMap() {
                this.mapStatus = false;
            },
            //删除
            deleteInstrument() {
                if (this.selectTable.length == 0) {
                    this.$message({
                        message: '请选择需要删除的物品！',
                        type: 'warning',
                        duration: '1500',
                    });
                } else {
                    let arr = [];
                    for (let i in this.selectTable) {   //batchShelfData
                        arr.push({
                            item_id: this.selectTable[i].item_id,
                        })
                    }
                    this.$confirm('此操作将永久删除被选中的数据, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.$axios.post('/item-del', {
                            item: arr
                        }).then((response) => {
//							console.log(response)
                            if (response.data.status == 0) {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功!',
                                    duration: '1500',
                                });
                                this.showRecords = false;
                                this.selectTable = [];
                                this.$refs.selTable.clearSelection();
                                this.handleCurrentChange(1);
                            }
                        }).catch((error) => {
//                            console.log(error)
                            this.$message({
                                type: 'warning',
                                message: error.message,
                                duration: '1500',
                            });
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除',
                            duration: '1500',
                        });
                    });
                }
            },
            //多选
            //被选中的数组
            instrumentSelectionChange(selection) {},
            //改变
            checkInstrumentSingle(selection, row) {
//				console.log(selection);
//				console.log(row)
                let data = true;    //勾选数据
                let index = '';     //数据下标
                for (let x in this.selectTable) {
                    if (this.selectTable[x].item_id == row.item_id) {
                        data = false;
                        index = x;  //勾选取消
                    }
                }
                if (data) {
                    this.selectTable.push(row);
                } else {
                    this.selectTable.splice(index, 1);
                }
            },
            //全选
            checkInstrumentAll(selection) {
//                console.log(selection);
                if (selection.length == 0) {
                    //取消
                    for (let i in this.instrumentData) {
                        for (let x in this.selectTable) {
                            if (this.instrumentData[i].item_id == this.selectTable[x].item_id) {
                                this.selectTable.splice(x, 1);
                            }
                        }
                    }
                } else {
                    //全选当前页
                    if (this.selectTable.length == 0) {
                        //如果没有被选中的
                        this.selectTable = selection;
                    } else {
                        for (let i in selection) {
                            this.selectTable.push(selection[i])
                        }
                    }
                    this.selectTable = this.unique(this.selectTable);
                }
            },
            //对象数组去重
            unique(array) {
                var obj = {};
                return array.filter(function (item, index, array) {
                    //console.log(typeof item + JSON.stringify(item))
                    return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
                })
            },
            //分页
            handleCurrentChange(val) {
                //获取小类列表
                this.$axios.post('/list', {
                    item_type: "仪器设备",
                    number: 5,
                    page: val,
                    sort_id: this.$route.params.id,
                    status: this.instrumentOptionsValue,
                }).then((res) => {
                    let showList = [];
//                  console.log(res)
                    if (res.data.data) {
                        this.instrumentData = res.data.data.data;
                        this.pageTotal = res.data.data.total;
                        for (let i in this.instrumentData) {
                            if (this.instrumentData[i].photo == null) {
                                this.instrumentData[i].pic = null
                            } else {
                                this.instrumentData[i].pic = this.instrumentData[i].photo.split('/')[1]
                            }
                        }
                        //获取当前页被选中的
                        for (let x in this.instrumentData) {
                            for (let y in this.selectTable) {
                                if (this.instrumentData[x].item_id == this.selectTable[y].item_id) {
                                    showList.push(this.instrumentData[x])
                                }
                            }
                        }
                        this.$nextTick(function () {
                            this.checkedChoose(showList);
                        });
                    } else {
                        this.instrumentData = [];
                        this.pageTotal = 0;
                    }
                }).catch((err) => {})
            },
            //勾选被选中的
            checkedChoose(rows) {
                rows.forEach(row => {
                    this.$refs.selTable.toggleRowSelection(row, true);
                });
            },
            //查看记录
            checkList(val) {
//                console.log(val)
                if(val == null){
                } else {
                    this.tableIndex = 1;
                    this.frid_id = val.frid_id;
                    this.clickIndex = val.item_id;
                    this.$axios.post('/receive-list', {
                        frid_id: this.frid_id,
                        number: 9999999999999999,
                    }).then((response) => {
//                    console.log(response)
                        if (response.data.data.data == undefined) {
                            //wu
                            this.receivedData = [];
                        } else {
                            this.receivedData = response.data.data.data;
                        }
                        this.showRecords = true;
                    }).catch((error) => {})
                }
            },
            //切换记录
            changeTable(val) {
//                console.log(val)
                this.tableIndex = val;
                if (val == 1) {
                    //历史领用
                    this.$axios.post('/receive-list', {
                        frid_id: this.frid_id,
                        number: 9999999999999999,
                    }).then((response) => {
//                        console.log(response)
                        if (response.data.data.data == undefined) {
                            //wu
                            this.receivedData = [];
                        } else {
                            this.receivedData = response.data.data.data;
                        }
                    }).catch((error) => {})
                } else {
                    //历史归还
                    this.$axios.post('/restore-list', {
                        frid_id: this.frid_id,
                        number: 9999999999999999,
                    }).then((response) => {
                        console.log(response)
                        if (response.data.data.data == undefined) {
                            //wu
                            this.returnData = [];
                        } else {
                            this.returnData = response.data.data.data;
                        }
                    }).catch((error) => {})
                }
            },
        },
